const state = {
  active: false,
};
const getters = {
  active(state) {
    return state.active;
  },
};
const mutations = {
  active(state, active) {
    state.active = active;
  },
};
const actions = {
  active(context, active) {
    this.commit("loader/active", active);
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
