import Vue from "vue";
import VueResource from "vue-resource";

Vue.use(VueResource);

Vue.http.options.root = 'https://api.kdajbi.si/api/';

export function initAuth(store) {
    return new Promise((resolve, reject) => {
        if (store.getters["user/isLoggedIn"] === true) {
            resolve();
            return;
        }
        let auth = JSON.parse(localStorage.getItem("auth"));

        if (auth === null) {
            resolve();
            return;
        }

        store.commit("user/auth", auth);
        store
            .dispatch("user/userinfo")
            .then(() => {
                resolve();
            })
            .catch(error => {
                store.dispatch("user/logout");
                reject(error);
            });
    });
}
export function pushInterceptor(http, store, router) {
    http.interceptors.push((request, next) => {
        if (
            request.url.indexOf("questions") === -1 ||
            request.method !== "GET"
        ) {
            store.dispatch("loader/active", true);
        }

        if (store.getters["user/isLoggedIn"]) {
            request.headers.set(
                "Authorization",
                "Bearer " + store.getters["user/accessToken"]
            );
            request.headers.set("Accept", "application/json");
        }
        next(response => {
            let tokenRefreshNeeded =
                response.status === 401 &&
                response.url.indexOf("/api/v1/login") === -1 &&
                response.url.indexOf("/api/v1/refresh") === -1;

            if (!tokenRefreshNeeded) {
                store.dispatch("loader/active", false);
                return;
            }

            return new Promise((resolve, reject) => {
                store.dispatch("user/refresh").then(
                    () => {
                        store.dispatch("loader/active", false);
                        resolve(http(request));
                    },
                    error => {
                        store.dispatch("loader/active", false);
                        router.replace({ name: "Dashboard" });
                        reject(error);
                    }
                );
            });
        });
    });
}
