const state = {
  auth: null,
  user: null,
};
const getters = {
  accessToken(state) {
    return state.auth ? state.auth.accessToken : null;
  },
  refreshToken(state) {
    return state.auth ? state.auth.refreshToken : null;
  },
  isLoggedIn(state) {
    return !!state.auth;
  },
  auth(state) {
    return state.auth;
  },
  user(state) {
    return state.user;
  },
};
const mutations = {
  auth(state, auth) {
    localStorage.setItem("auth", JSON.stringify(auth));
    state.auth = auth;
  },
  user(state, user) {
    state.user = user;
  },
};
